.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 1000;
	background-color: rgba(0,0,0, 0.75);
}

.modal {
	position: fixed;
	top: 30vh;
	left: 10%;
	width: 80%;
	z-index: 10000;
	overflow: auto;
}

.header {
	background: #4f005f;
	padding: 1rem;
  }
  
  .header h2 {
	margin: 0;
	color: white;
  }
  
  .content {
	padding: 1rem;
  }

  @media (min-width: 768px) {
	.modal {
	  left: calc(50% - 20rem);
	  width: 40rem;
	}
  }